@import "../../../styles/colors/colors.scss";
@import "../../../styles/text-styles/textStyles.scss";

.search-wrapper {
	background: $light-font;
	width: 345px;
	height: 52px;
	box-sizing: border-box;
	border-radius: 12px;
	position: relative;

	input {
		border: none;
		background: transparent;
		width: 100%;
		height: 100%;
		border-radius: 12px;
		@extend .body-1;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		text-align: left;
		padding-left: 16px;
		color: $placeholder-font;
	}
	img {
		position: absolute;
		right: 16px;
		top: 50%;
		transform: translateY(-50%);
		width: 32px;
		height: 32px;
	}

	&.-generic {
		background: transparent;
		border: 1px solid $inactive-color;
		img {
			width: 24px;
			height: 24px;
		}
		input {
			@extend .subtitle-1;
		}

		&.-focused {
			border-color: $dark-font;
			input {
				color: $dark-font;
			}
		}
	}
}
